import * as actionTypes from "../actions/actionTypes";

const initialState = {
    featProj: null,
    otherProj: null,
    designProj: null,
    errorMsgs: {
        featProjErrMsg: "",
        otherProjErrMsg: "",
        designProjErrMsg: "",
    },
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.FETCH_PROJ_FAILED:
            return {
                ...state,
                [action.error.type.slice(0, -6)]: null,
                errorMsgs: {
                    ...state.errorMsgs,
                    [action.error.type]: action.error.msg,
                },
            };
        case actionTypes.FETCH_FEAT_PROJ:
            return {
                ...state,
                featProj: action.proj,
                errorMsgs: {
                    ...state.errorMsgs,
                    featProjErrMsg: "",
                },
            };
        case actionTypes.FETCH_OTHER_PROJ:
            return {
                ...state,
                otherProj: action.proj,
                errorMsgs: {
                    ...state.errorMsgs,
                    otherProjErrMsg: "",
                },
            };
        case actionTypes.FETCH_DESIGN_PROJ:
            return {
                ...state,
                designProj: action.proj,
                errorMsgs: {
                    ...state.errorMsgs,
                    designProjErrMsg: "",
                },
            };
        default:
            return state;
    }
};

export default reducer;
